<template>
  <view class="project-info">
    <u-navbar
        :borderBottom="false"
        :customBack="back"
        backIconName="arrow-leftward"
        titleSize="34"
        :background="background"
        title="项目信息"></u-navbar>
    <view class="margin-normal">
      <u-image
          border-radius="16"
          :src="projectImageSrc"
          width="100%"
          height="210px"
          :lazy-load="true"></u-image>
    </view>

    <nnkjc-detail-group>
      <nnkjc-detail-item
          detailTitle="项目名称"
          :detailContent="detail.name"
      >
      </nnkjc-detail-item>
      <nnkjc-detail-item
          detailTitle="项目位置"
          :detailContent="detail.areaName"
      >
      </nnkjc-detail-item>
      <nnkjc-detail-item
          detailTitle="竣工日期"
          :detailContent="detail.finishDate"
      >
      </nnkjc-detail-item>
      <nnkjc-detail-item
          detailTitle="建筑面积"
          :detailContent="detail.buldArea"
      >
      </nnkjc-detail-item>
      <nnkjc-detail-item
          detailTitle="权属单位"
          :detailContent="detail.comName"
      >
      </nnkjc-detail-item>
      <nnkjc-detail-item
          detailTitle="物业单位"
          :detailContent="detail.propertyComName"
      >
      </nnkjc-detail-item>
      <nnkjc-detail-item
          detailTitle="项目介绍"
          :gap="false"
      >
        <view slot="content">
          <u-parse :html="detail.content"></u-parse>
        </view>
      </nnkjc-detail-item>
    </nnkjc-detail-group>
  </view>
</template>

<script>
  import CONFIG from "../../../config";
  export default {
    name: "projectInfo",
    data() {
      return {
        detail: {
          name: "",
          areaName: "",
          finishDate: "",
          buldArea: "",
          comName: "",
          propertyComName: "",
          content: ""
        },
        background: {
          'background': '#f2f2f8'
        },
        projectImageSrc: ""
      }
    },
    created() {
      this.getDetail();
    },
    methods: {
      back() {
        uni.navigateBack({
          delta: 1
        })
      },
      getDetail() {
        this.$u
            .post("/index/projectView", this.searchParams)
            .then(res => {
              if (res.data) {
                this.detail = res.data;
                if(this.detail.buldArea) {
                  this.detail.buldArea += "平方米";
                }
                if(this.detail.pic) {
                  this.projectImageSrc = CONFIG.baseUrl + this.detail.pic;
                }
              }
            })
            .catch(err => {
              console.log(err);
            });
      }
    }
  }
</script>

<style scoped>
  .project-info {
    padding-bottom: 20px;
  }

</style>